.QueueItem {
  display: -ms-flexbox;
  display: flex;
  padding: 0.5em;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid var(--white-05);
}

.QueueItem:hover {
  background: var(--white-05);
}

.QueueItem-selected {
  color: var(--primary);
}

.QueueItem-helper {
  background: var(--background);
  opacity: 0.9;
}

.QueueItem-thumb {
  width: calc(60px - 1em);
  margin-right: 0.5em;
}

.QueueItem-text {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}

.QueueItem-title {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.QueueItem-artist {
  color: var(--white-50);
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.QueueItem-time {
  color: var(--white-50);
}



